import React, { useRef, useState } from "react";
import { Link } from "react-router-dom";
import { Card, Media } from "react-bootstrap";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Container from "react-bootstrap/Container";
import { Alert, Image } from "react-bootstrap";

import Upload from "../../assets/upload.png";

import styles from "../../styles/PostCreateEditForm.module.css";
import appStyles from "../../App.module.css";
import btnStyles from "../../styles/Button.module.css";

import Asset from "../../components/Asset";
import Avatar from "../../components/Avatar";

import { useHistory } from "react-router-dom/cjs/react-router-dom.min";
import { axiosReq } from "../../api/axiosDefaults";
import { useRedirect } from "../../hooks/useRedirect";


function PostPreview({ postData, onEdit, onSubmit }) {
  const owner = "Username";
  const profile_image = "";


  return (
    <Card className={styles.Post}>
      <Card.Body>
        <Media className="align-items-center justify-content-between">
          <Link to="#">
            <Avatar src={profile_image} height={45} />
            {owner}
          </Link>
          <div className="d-flex align-items-center">
            <Button className={`${btnStyles.Button} ${btnStyles.Blue}`} onClick={onEdit}>Go Back</Button>
            <Button className={`${btnStyles.Button} ${btnStyles.Blue}`} onClick={onSubmit}>Submit</Button>
          </div>
        </Media>
      </Card.Body>
      <Link to="#">
      <div className="d-flex justify-content-center">
        <Card.Img className="img-fluid" style={{ width:'30%' }} src={postData.image} alt={postData.title} />
        </div>
      </Link>
      <Card.Body>
        {postData.title && <Card.Title className="text-center">{postData.title}</Card.Title>}
        {postData.summary && <Card.Title className="text-center">{postData.summary}</Card.Title>}
        {postData.content && <Card.Text className="text-center">{postData.content}</Card.Text>}
      </Card.Body>
    </Card >
  );
}


function PostCreateForm() {
  useRedirect("loggedOut");

  const [showPreview, setShowPreview] = useState(false);
  const [errors, setErrors] = useState({});
  const [postData, setPostData] = useState({
    title: "",
    content: "",
    image: "",
    summary: "",
  });
  const { title, content, image, summary } = postData;

  const [imageFile, setImageFile] = useState(null);

  const imageInput = useRef(null);
  const history = useHistory();

  const handleChange = (event) => {
    setPostData({
      ...postData,
      [event.target.name]: event.target.value,
    });
  };

  const handleChangeImage = (event) => {
    if (event.target.files.length) {
      const selectedFile = event.target.files[0];
      setImageFile(selectedFile);  // Save the file object in state
      URL.revokeObjectURL(image);
      setPostData({
        ...postData,
        image: URL.createObjectURL(selectedFile),
      });
    }
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    const formData = new FormData();

    formData.append('title', title);
    formData.append('content', content);
    formData.append("summary", summary);


    if (imageFile) {
      formData.append('image', imageFile);
    } else {
      // Handle the case where the image might not be available.
      setErrors({
        ...errors,
        image: ["Please provide a valid image for the post."]
      });
      return; // Don't proceed with the submission.
    }

    try {
      await axiosReq.get('/dj-rest-auth/csrf/');
      await axiosReq.post('/posts/', formData);
      history.push(`/`);
    } catch (err) {
      if (err.response?.status !== 401) {
        setErrors(err.response?.data);
      }
    }
  };


  const textFields = (
    <div className="text-center">
      <Form.Group>
        <Form.Label>Title</Form.Label>
        <Form.Control
          type="text"
          name="title"
          value={title}
          onChange={handleChange}
        />
      </Form.Group>
      {errors?.title?.map((message, idx) => (
        <Alert variant="warning" key={idx}>
          {message}
        </Alert>
      ))}
      <Form.Group>
        <Form.Label>Caption</Form.Label>
        <Form.Control
          type="text"
          name="summary"
          value={summary}
          onChange={handleChange}
        />
      </Form.Group>
      {errors?.summary?.map((message, idx) => (
        <Alert variant="warning" key={idx}>
          {message}
        </Alert>
      ))}
      <Form.Group>
        <Form.Label>Content</Form.Label>
        <Form.Control
          as="textarea"
          rows={3}
          name="content"
          value={content}
          onChange={handleChange}
        />
      </Form.Group>
      {errors?.content?.map((message, idx) => (
        <Alert variant="warning" key={idx}>
          {message}
        </Alert>
      ))}
    </div>
  );

  return (
    <Form onSubmit={handleSubmit}>
      {showPreview ? (
        <PostPreview postData={postData} onEdit={() => setShowPreview(false)} onSubmit={handleSubmit} />
      ) : (
        <>
          <Row>
            <Col className="py-2 p-0 p-md-2" md={7} lg={8}>
              <Container className={`${appStyles.Content} ${styles.Container} d-flex flex-column justify-content-center`}>
                <Form.Group className="text-center">
                  {image ? (
                    <>
                      <figure>
                        <Image className={appStyles.Image} src={image} rounded />
                      </figure>
                      <div>
                        <Form.Label className={`${btnStyles.Button} ${btnStyles.Blue} btn`} htmlFor="image-upload">
                          Change the image
                        </Form.Label>
                      </div>
                    </>
                  ) : (
                    <Form.Label className="d-flex justify-content-center" htmlFor="image-upload">
                      <Asset src={Upload} message="Click or tap to upload an image" />
                    </Form.Label>
                  )}
                  <Form.File id="image-upload" accept="image/*" onChange={handleChangeImage} ref={imageInput} />
                </Form.Group>
                {errors?.image?.map((message, idx) => (
                  <Alert variant="warning" key={idx}>
                    {message}
                  </Alert>
                ))}
                <div className="d-md-none">{textFields}</div>
              </Container>
            </Col>
            <Col md={5} lg={4} className="d-none d-md-block p-0 p-md-2">
              <Container className={appStyles.Content}>{textFields}</Container>
            </Col>
          </Row>
          <div className="text-center mt-4">
            <Button
              className={`${btnStyles.Button} ${btnStyles.Blue}`}
              onClick={() => history.goBack()}
            >
              Cancel
            </Button>
            <Button className={`${btnStyles.Button} ${btnStyles.Blue}`} onClick={() => setShowPreview(!showPreview)}>
              Preview
            </Button>
            <Button className={`${btnStyles.Button} ${btnStyles.Blue}`} type="submit">
              Post it!
            </Button>
          </div>
        </>
      )}
    </Form>
  );

}
export default PostCreateForm;
